import React, { useState } from 'react';
import {
    Box,
    Button,
    Dialog,
    DialogContent,
    DialogTitle,
    IconButton,
    TextField,
} from '@mui/material';
import CloseIcon from '@mui/icons-material/Close';
import { TwitterPicker } from 'react-color';
import notify from 'lib/notify';
import { sendApiRequest } from 'lib/api/public';
import { renderField } from '../../components/utils';

type Props = {
    onClose: () => void;
    onSuccess: () => void;
    open: boolean;
    status?: {
        _id: string,
        name: string,
        color: string,
    }
};

const StatusForm = ({ open, onClose, onSuccess, status }: Props) => {
    const statusO = status || { _id: null, name: '', color: '' };

    const [name, setName] = useState(statusO.name);
    const [color, setColor] = useState(statusO.color);
    const [saving, setSaving] = useState(false);

    const onSave = async () => {
        if (!name || !color) {
            notify('Please fill the required fields');
            return;
        }

        setSaving(true);

        try {
            await sendApiRequest({
                path: '/candidates/statuses/manage',
                method: 'POST',
                data: {
                    _id: statusO._id,
                    name,
                    color
                }
            });

            onSuccess();

            setSaving(false);
        } catch (error) {
            setSaving(false);
            notify(error);
        }

    };

    const renderSingleField = (value: string, label: string, onChange, required=false) => {
        return (
            <>
                {renderField({ label, required, renderControl: () => {
                    return <TextField fullWidth value={value} onChange={(e) => onChange(e.target.value)} />
                }})}
            </>
        )
    }

    return (
        <Dialog onClose={onClose} aria-labelledby="modal-title" open={open} PaperProps={{ sx: { width: '100%' }}}>
            <DialogTitle id="modal-title">
                <Box display="flex" alignItems="center">
                    <Box flexGrow={1} >{ statusO.name ? 'Edit status' : 'Create a new status'}</Box>
                    <Box>
                        <IconButton onClick={onClose}>
                            <CloseIcon />
                        </IconButton>

                        <Button variant="contained" color="success" onClick={onSave} disabled={saving}>
                            Save
                        </Button>
                    </Box>
                </Box>
            </DialogTitle>

            <DialogContent style={{ backgroundColor: '#f8f8f8' }}>
                <br />

                {renderSingleField(name, 'Title', setName, true)}

                {renderField({ label: 'Color', required: true, renderControl: () => {
                     return <TwitterPicker color={color} onChangeComplete={(e) => {
                        setColor(e.hex)
                     }} />
                }})}
            </DialogContent>
        </Dialog>
    );
};

export default StatusForm;
