import React, { useEffect, useState } from 'react';
import AuthLayout from '../../components/AuthLayout';
import { Button, Paper, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, IconButton } from '@mui/material';
import AddIcon from '@mui/icons-material/Add';
import CancelIcon from '@mui/icons-material/Cancel';
import { sendApiRequest } from '../../lib/api/public';
import StatusForm from './StatusForm';
import TriggerForm from './TriggerForm';
import confirm from 'lib/confirm';

const CandidateStatus = () => {
  const [isLoading, setLoading] = useState(false);
  const [statuses, setStatuses] = useState([]);

  const [selectedStatus, setSelectedStatus] = useState<any>();
  const [selectedTrigger, setSelectedTrigger] = useState<any>();
  const [showStatusForm, setShowStatusForm] = useState(false);
  const [showTriggerForm, setShowTriggerForm] = useState(false);

  const getStatuses = () => {
    setLoading(true);

    sendApiRequest({
      path: `/candidates/statuses`,
      method: 'GET',
    }).then((response: any) => {
      setLoading(false);

      if (!response.error) {
        setStatuses(response.list);
      }
    })
  }

  const deleteTrigger = (triggerId: string) => {
    sendApiRequest({
      path: `/candidates/statuses/manage-triggers`,
      data: {
        action: 'delete',
        _id: triggerId,
      },
    }).then(() => {
      getStatuses();
    })
  }

  const deleteStatus = (statusId: string) => {
    sendApiRequest({
      path: `/candidates/statuses/manage`,
      data: {
        action: 'delete',
        _id: statusId,
      },
    }).then(() => {
      getStatuses();
    })
  }

  useEffect(() => {
    getStatuses();
  }, [])

  const renderStatusForm = () => {
    if (!showStatusForm) {
      return null;
    }

    return (
      <StatusForm
        status={selectedStatus}
        open={showStatusForm}
        onClose={() => setShowStatusForm(false)}
        onSuccess={() => { setShowStatusForm(false); getStatuses(); }}
      />
    )
  }

  const renderTriggerForm = () => {
    if (!showTriggerForm || !selectedStatus) {
      return null;
    }

    return (
      <TriggerForm
        statusId={selectedStatus._id}
        trigger={selectedTrigger}
        onClose={() => setShowTriggerForm(false)}
        onSuccess={() => { setShowTriggerForm(false); getStatuses(); }}
      />
    )
  }

  return (
    <AuthLayout title='Candidate Pipeline' isLoading={isLoading}>
      <div
        style={{
          float: 'right',
        }}
      >
        <Button
          onClick={() => { setSelectedStatus(null); setShowStatusForm(true) }}
          variant="contained"
        >
          + Add status
        </Button>
      </div>

      <div style={{ clear: 'both' }} />

      <br />

      {renderStatusForm()}
      {renderTriggerForm()}

      <TableContainer component={Paper}>
        <Table sx={{ minWidth: 650 }} size='small'>
          <TableHead>
            <TableRow>
              <TableCell>#</TableCell>
              <TableCell>Name</TableCell>
              <TableCell>Color</TableCell>
              <TableCell>Triggers</TableCell>
              <TableCell></TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {statuses.map((status, index) => (
              <TableRow
                key={status._id}
              >
                <TableCell sx={{ width: '5px' }}>{index + 1}</TableCell>
                <TableCell sx={{ maxWidth: '200px' }}>{status.name}</TableCell>
                <TableCell>
                  <div style={{ display: 'flex' }}>
                    <span
                      style={{
                        display: 'inline-block',
                        height: '20px',
                        width: '20px',
                        backgroundColor: status.color,
                        borderRadius: '5px',
                        marginRight: '5px'
                      }}
                      />

                      {status.color}
                  </div>
                </TableCell>

                <TableCell style={{ display: 'flex', alignItems: 'center' }}>
                  <IconButton sx={{ cursor: 'pointer' }} onClick={() => {
                    setShowTriggerForm(true);
                    setSelectedStatus(status);
                    setSelectedTrigger(null);
                  }}>
                    <AddIcon color="success" />
                  </IconButton>

                  {(status.triggers || []).map((trigger) => (
                    <div>
                      <a href="#" style={{ cursor: 'pointer' }} key={trigger._id} onClick={() => {
                        setSelectedStatus(status);
                        setSelectedTrigger(trigger);
                        setShowTriggerForm(true);
                      }}>
                        {trigger.description}
                      </a>

                      <IconButton onClick={() => {
                        confirm({ title: 'Delete trigger', message: 'Are you sure you want to delete this trigger?', onAnswer: (answer) => {
                          if (answer) {
                            deleteTrigger(trigger._id);
                          }
                        }})
                      }}>
                        <CancelIcon color='error' />
                      </IconButton>
                    </div>
                  ))}
                </TableCell>

                <TableCell align='right'>
                    <Button
                      variant='contained'
                      size="small"
                      color="inherit"
                      onClick={() => {
                        setSelectedStatus(status);
                        setShowStatusForm(true);
                      }}>
                      Edit
                    </Button>

                    {' '}

                    <Button
                      variant='contained'
                      size="small"
                      color="error"
                      onClick={() => {
                        deleteStatus(status._id);
                      }}
                    >
                      Delete
                    </Button>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
    </AuthLayout>
  );
};

export default CandidateStatus;
